import {
  Dimensions,
  LogBox,
  Platform,
  ScrollView,
  Text,
  View
} from 'react-native';
import {
  useEditUserMutation,
  useGetUserInfoQuery
} from '../features/api/usersApi';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { selectDarkMode, selectUserKey } from '../app/appDataSlice';
import React, { useEffect, useRef, useState } from 'react';
import BottomSheet from '@gorhom/bottom-sheet/lib/typescript/components/bottomSheet/BottomSheet';
import CalendarFunctions from '../features/calendar/CalendarFunctions';
import {
  classSelected,
  selectClassesList
} from '../features/classPages/classPagesSlice';
import renderCustomEvent from '../features/calendar/customEvent';
import renderItem from '../features/calendar/ScheduledItem';
import { CurrentTermLabel } from '../features/classPages/ClassesList';
import SingleClassPage from '../features/classPages/SingleClassPage';
import { base_URL } from '../features/notifications/constants';
import { useNavigation } from '@react-navigation/native';

//LogBox.ignoreAllLogs();

export default function CalendarRender({
  route: {
    params: { overrideData }
  }
}) {
  const isWeb = Platform.OS === 'web';
  const [editUser] = useEditUserMutation();
  const userKey = useAppSelector(selectUserKey);
  const isThreeDay = useAppSelector(
    (state) => state.appData.calendar.isThreeDay
  );
  const dispatch = useAppDispatch();
  const bottomSheetRef = useRef<BottomSheet>(null);
  const darkMode: boolean = useAppSelector(selectDarkMode);
  const calendarFun = new CalendarFunctions();
  const [necessaryCourseInfo, setNecessaryCourseInfo] = useState(null);
  const universalHeight = (Dimensions.get('window').height - 50) / 12;
  const threeDayWidth = (Dimensions.get('window').width - 75) / 3;
  const fiveDayWidth = (Dimensions.get('window').width - 75) / 5;
  const [classes, setClasses] = useState({
    Monday: [],
    Tuesday: [],
    Wednesday: [],
    Thursday: [],
    Friday: [],
    Saturday: [],
    Sunday: []
  });
  const days = [
    { day: 'Mon', id: 'Monday' },
    { day: 'Tues', id: 'Tuesday' },
    { day: 'Wed', id: 'Wednesday' },
    { day: 'Thurs', id: 'Thursday' },
    { day: 'Fri', id: 'Friday' },
    { day: 'Sat', id: 'Saturday' },
    { day: 'Sun', id: 'Sunday' }
  ];
  const times = [
    '12:00 AM',
    '1:00 AM',
    '2:00 AM',
    '3:00 AM',
    '4:00 AM',
    '5:00 AM',
    '6:00 AM',
    '7:00 AM',
    '8:00 AM',
    '9:00 AM',
    '10:00 AM',
    '11:00 AM',
    '12:00 PM',
    '1:00 PM',
    '2:00 PM',
    '3:00 PM',
    '4:00 PM',
    '5:00 PM',
    '6:00 PM',
    '7:00 PM',
    '8:00 PM',
    '9:00 PM',
    '10:00 PM',
    '11:00 PM'
  ];

  const { data: userData } = useGetUserInfoQuery(userKey);
  const addedClasses = userData?.addedClasses?.classes;
  const allClassData = useAppSelector(selectClassesList);
  const [overrideClassesToRender, setOverrideClassesToRender] = useState([]);
  const currentSection = useAppSelector(
    (state) => state.appData.classPages.currentSection
  );

  useEffect(() => {
    try {
      if (overrideData.length !== 0) {
        getOverrideCourseData().then(() => {});
      }
    } catch (e) {
      console.log(e);
    }
    if (isWeb) {
      verticalRef.current.scrollTo({
        y: Dimensions.get('screen').height / 1.9
      });
    }
  }, []);

  async function getOverrideCourseData() {
    const stringOverrideData = JSON.stringify(
      overrideData.map((course) => {
        return course.courseCode.slice(0, -3);
      })
    );
    const response = await fetch(
      base_URL +
        `/classes/claremontColleges?listOfCourses=${stringOverrideData}&courseTerm=${overrideData[0].courseTerm}`,
      {
        headers: {
          userKey,
          'content-type': 'application/json'
        },
        method: 'GET'
      }
    );
    setOverrideClassesToRender(await response.json());
  }

  const renderCustomEvents = (elem) => {
    const finalRender = [];
    if (!userData.customEvents) {
      return null;
    }
    for (const event of userData.customEvents) {
      if (event.days.includes(elem.id)) {
        finalRender.push(renderCustomEvent(event, userData, userKey, editUser));
      } else if (
        event.date &&
        eventIsThisDay(elem.id, new Date(event.date)) &&
        showThisWeek(new Date(event.date))
      ) {
        finalRender.push(renderCustomEvent(event, userData, userKey, editUser));
      }
    }
    return finalRender;
  };

  function eventIsThisDay(dayString: string, date: Date) {
    const dayOfWeek = adjustDays(date.getDay());
    return dayString === days[dayOfWeek].id;
  }

  function adjustDays(dayOfWeek) {
    if (dayOfWeek === 0) {
      dayOfWeek = 6;
    } else {
      dayOfWeek -= 1;
    }
    return dayOfWeek;
  }

  function getMs(hours, minutes, seconds) {
    let final = 0;
    final += hours * 3600000;
    final += minutes * 60000;
    final += seconds * 1000;
    return final;
  }

  function showThisWeek(dateOfEvent: Date) {
    const currentDate = new Date();
    const currentDayOfWeek = adjustDays(currentDate.getDay());
    const startOfWeek = new Date(
      currentDate.valueOf() -
        currentDayOfWeek * 86400000 -
        getMs(
          currentDate.getHours(),
          currentDate.getMinutes(),
          currentDate.getSeconds()
        )
    );
    const endOfWeek = new Date(
      currentDate.valueOf() +
        (6 - currentDayOfWeek) * 86400000 +
        getMs(
          23 - currentDate.getHours(),
          59 - currentDate.getMinutes(),
          59 - currentDate.getSeconds()
        )
    );
    return !(
      dateOfEvent.valueOf() > endOfWeek.valueOf() ||
      dateOfEvent.valueOf() < startOfWeek.valueOf()
    );
  }

  const handleClassSelected = (courseCode: string) => {
    dispatch(classSelected(courseCode));
    if (Platform.OS === 'web') {
      navigation.navigate('SingleClassPage');
    } else {
      bottomSheetRef.current?.snapToIndex(0);
    }
  };

  function renderDashes() {
    const final = [];
    for (let i = 0; i < 24; i++) {
      final.push(
        <View
          key={i}
          style={{
            width: (isThreeDay ? threeDayWidth : fiveDayWidth) * 0.73016,
            marginLeft: (isThreeDay ? threeDayWidth : fiveDayWidth) * 0.14286,
            top: universalHeight * i,
            position: 'absolute',
            height: universalHeight,
            borderBottomWidth: Platform.OS === 'web' ? 1.2 : 0.7,
            borderBottomColor: darkMode ? 'rgb(68,68,68)' : 'rgb(204,204,204)'
          }}
        />
      );
    }
    return final;
  }

  const renderClasses = (elem) => {
    const classes = [];
    Object.keys(addedClasses).map((term) => {
      if (term === currentSection) {
        Object.keys(addedClasses[term]).map((masterCourseCode) => {
          for (const courseCode of addedClasses[term][masterCourseCode]) {
            try {
              const data = calendarFun.getFullClassData(
                courseCode,
                allClassData
              );
              const data2 = data.times.find(
                (time) => time.courseCode === courseCode
              );
              for (const meetingTime of data2.courseSchedule) {
                for (const day in meetingTime.daysOfTheWeek) {
                  if (meetingTime.daysOfTheWeek[day] === elem.id) {
                    classes.push(
                      renderItem(
                        data,
                        meetingTime,
                        courseCode,
                        handleClassSelected
                      )
                    );
                  }
                }
              }
            } catch (e) {
              console.warn(e);
            }
          }
        });
      }
    });
    return classes;
  };

  function renderClassesForOverride(elem) {
    const classes = [];
    if (overrideClassesToRender.length > 0) {
      for (const courseCode of overrideData.map((course) => {
        return course.courseCode;
      })) {
        try {
          const data = overrideClassesToRender.find((course) =>
            course.times.some(
              (courseTime) => courseTime.courseCode === courseCode
            )
          );
          const data2 = data.times.find(
            (time) => time.courseCode === courseCode
          );
          for (const meetingTime of data2.courseSchedule) {
            for (const day in meetingTime.daysOfTheWeek) {
              if (meetingTime.daysOfTheWeek[day] === elem.id) {
                classes.push(
                  renderItem(data, meetingTime, courseCode, handleClassSelected)
                );
              }
            }
          }
        } catch (e) {
          console.warn(e);
        }
      }
    }
    return classes;
  }

  function masterRender(elem) {
    console.log('override: ' + (overrideData.length !== 0));
    if (overrideClassesToRender.length !== 0) {
      return <View>{renderClassesForOverride(elem)}</View>;
    } else {
      return (
        <View>
          {userData?.addedClasses && renderClasses(elem)}
          {userData?.addedClasses && renderCustomEvents(elem)}
        </View>
      );
    }
  }

  const navigation = useNavigation();

  const topBar = useRef(null);
  const verticalRef = useRef(null);
  const [startingOffset, setStartingOffset] = useState({
    x: 0,
    y: isWeb ? 500 : 525
  });

  return (
    <View style={{ flex: 1, backgroundColor: darkMode ? null : 'white' }}>
      {/* Times */}
      {overrideData.length === 0 && (
        <CurrentTermLabel
          navigation={navigation}
          currentSection={currentSection}
          style={{ margin: 10 }}
        />
      )}
      <ScrollView
        horizontal
        snapToInterval={isThreeDay ? threeDayWidth : fiveDayWidth}
        showsHorizontalScrollIndicator={false}
        style={{ marginLeft: 75, height: isWeb ? 43 : undefined }}
        ref={topBar}
      >
        <View style={{ flexDirection: 'row' }}>
          {days.map((elem, index) => {
            return (
              <View key={index}>
                <View
                  style={{
                    borderColor: darkMode ? '#7a7a7a' : '#525252',
                    borderBottomWidth: 1,
                    width: '100%'
                  }}
                >
                  <Text
                    style={{
                      textAlign: 'center',
                      color: darkMode ? '#7a7a7a' : '#525252',
                      width: isThreeDay ? threeDayWidth : fiveDayWidth
                    }}
                  >
                    {elem.day}
                  </Text>
                </View>
              </View>
            );
          })}
        </View>
      </ScrollView>
      <ScrollView
        contentOffset={startingOffset}
        showsVerticalScrollIndicator={isWeb}
        bounces={false}
        ref={verticalRef}
      >
        <View style={{ flexDirection: 'row' }}>
          <View
            style={{
              justifyContent: 'space-around'
              // height: 1000,
            }}
          >
            {times.map((time, index) => {
              return (
                <Text
                  key={index}
                  style={{
                    height: universalHeight,
                    width: 75,
                    borderWidth: 1,
                    borderColor: darkMode ? '#7a7a7a' : '#525252',
                    textAlign: 'center',
                    color: darkMode ? '#7a7a7a' : '#525252'
                  }}
                >
                  {time}
                </Text>
              );
            })}
          </View>
          {/* Classes */}
          <ScrollView
            horizontal
            snapToInterval={isThreeDay ? threeDayWidth : fiveDayWidth}
            decelerationRate="normal"
            showsHorizontalScrollIndicator={isWeb}
            bounces={false}
            scrollEventThrottle={1}
            onScroll={(e) =>
              topBar.current.scrollTo({
                x: e.nativeEvent.contentOffset.x,
                animated: false,
                useNativeDriver: true
              })
            }
            onMomentumScrollBegin={(e) =>
              topBar.current.scrollTo({
                x: e.nativeEvent.contentOffset.x,
                animated: false,
                useNativeDriver: true
              })
            }
          >
            <View style={{ flexDirection: 'row' }}>
              {days.map((elem, index) => {
                return (
                  <View key={index}>
                    {renderDashes()}
                    <View
                      key={elem.day}
                      style={{
                        width: isThreeDay ? threeDayWidth : fiveDayWidth
                      }}
                    >
                      {masterRender(elem)}
                    </View>
                    <View
                      style={{
                        height: '100%',
                        width: 1,
                        backgroundColor: darkMode ? '#7a7a7a' : '#525252'
                      }}
                    />
                  </View>
                );
              })}
            </View>
          </ScrollView>
        </View>
      </ScrollView>
      {Platform.OS !== 'web' && Object.entries(overrideData).length === 0 && (
        <SingleClassPage bottomSheetRef={bottomSheetRef} />
      )}
    </View>
  );
}
