import { useNavigation } from '@react-navigation/native';
import React from 'react';
import {
  Alert,
  Dimensions,
  Platform,
  Text,
  TouchableOpacity,
  View
} from 'react-native';

import CalendarFunctions from './CalendarFunctions';
import { useAppSelector } from '../../app/hooks';
import {
  useGetCurrentSectionQuery,
  useRemoveClassMutation
} from '../api/classPagesApi';

export default function renderItem(
  masterClassInfo,
  meetingTime,
  courseCode,
  handleClassSelected = (string: string) => {}
) {
  const calendarFun = new CalendarFunctions();
  const universalHeight = (Dimensions.get('window').height - 50) / 12;
  const minuteHeight = universalHeight / 60;

  const [startHour, startMinute] = meetingTime.startTime.split(':');
  const [endHour, endMinute] = meetingTime.endTime.split(':');
  const color = calendarFun.stringToColour(masterClassInfo.masterCourseCode);
  const code = courseCode.split(' ');
  return (
    <TouchableOpacity
      key={Math.random()}
      style={{
        backgroundColor: color,
        top: universalHeight * startHour + startMinute * minuteHeight,
        height:
          universalHeight * (endHour - startHour) +
          calendarFun.getMinutesDiff(startMinute, endMinute, minuteHeight),
        width: '100%',
        position: 'absolute',
        margin: 0,
        justifyContent: 'center'
      }}
      onPress={() => {
        handleClassSelected(masterClassInfo.masterCourseCode);
      }}
      onLongPress={
        () => {} /* {
                    Alert.alert(
                        courseCode,
                        null,
                        [
                            {
                                text: 'Delete',
                                onPress: () => {

                                },
                            },
                            {
                                text: 'Cancel',
                                onPress: () => console.warn('Cancel Pressed'),
                                style: 'cancel',
                            },
                        ],
                        { cancelable: true }
                    );
                } */
      }
    >
      {Platform.OS !== 'web' && (
        <Text
          adjustsFontSizeToFit
          numberOfLines={5}
          style={{
            color: parseInt(color.slice(1), 16) > 9079434 ? 'black' : 'white',
            fontSize: 11,
            marginHorizontal: 3,
            textAlign: 'center'
          }}
        >
          {code[0]} {code[1]}
          {'\n'}
          {code[2]}
          {meetingTime.classLocation
            ? `\n(${meetingTime.classLocation})`
            : null}
        </Text>
      )}
      {Platform.OS === 'web' && (
        <View style={{ flexDirection: 'column' }}>
          <Text
            style={{
              color: parseInt(color.slice(1), 16) > 9079434 ? 'black' : 'white',
              fontSize: 15,
              fontWeight: 'bold',
              marginHorizontal: 3,
              textAlign: 'center'
            }}
          >
            {code[0]} {code[1]} {code[2]}
          </Text>
          <Text
            style={{
              color: parseInt(color.slice(1), 16) > 9079434 ? 'black' : 'white',
              fontSize: 13,
              marginHorizontal: 3,
              textAlign: 'center',
              marginTop: 5
            }}
          >
            {meetingTime.classLocation
              ? `(${meetingTime.classLocation})`
              : null}
          </Text>
        </View>
      )}
    </TouchableOpacity>
  );
}
